<template>
  <div class="home">
    <md-list
      class="md-double-line"
      v-for="list in data.outlay"
      :key="list.id"
      :class="list.color"
    >
      <md-list-item>
        <md-button v-on:click="showAlert(list.id)" class="md-icon-button">
          <md-icon>remove</md-icon>
        </md-button>
        <div class="md-list-item-text">
          <span>{{ list.name }}</span>
          <span>{{ formatedPrice(list.price) }} Ft</span>
        </div>
        <span class="item-bought-date">{{ list.create_date }}</span>
        <!--<md-icon v-if="today.type">keyboard_arrow_up</md-icon>
          <md-icon v-else>keyboard_arrow_down</md-icon>-->
      </md-list-item>
      <md-divider></md-divider>
    </md-list>
    <div class="new-item-menu">
      <md-field>
        <label>Termék</label>
        <md-input v-model="data.product_name"></md-input>
      </md-field>
      <md-field>
        <label>Ára</label>
        <md-input v-model="data.product_price"></md-input>
      </md-field>
      <md-field>
        <label for="movie">Típus</label>
        <md-select v-model="data.selected" name="item_type" id="item_type">
          <md-option
            v-for="option in data.options"
            :key="option.id"
            v-bind:value="option.id"
            >{{ option.name }}</md-option
          >
        </md-select>
      </md-field>
      <md-button
        v-on:click="uploadNewItem()"
        class="md-icon-button md-raised md-primary"
      >
        <md-icon>control_point</md-icon>
      </md-button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "home",

  data() {
    return {
      data: []
    };
  },
  computed: {},
  mounted() {
    axios
      .post(process.env.VUE_APP_GET_WALLET_API, JSON.stringify({ list: true }))
      .then(response => (this.data = response.data))
      .catch(err => console.log(err));
  },
  methods: {
    formatedPrice(price) {
      return new Number(price).toLocaleString("hu-HU");
    },
    uploadNewItem() {
      axios
        .post(
          process.env.VUE_APP_GET_WALLET_API,
          JSON.stringify({
            create: this.data.product_name,
            category_id: this.data.selected,
            money: this.data.product_price
          })
        )
        .then(response => (this.data.outlay = response.data))
        .catch(err => console.log(err));
    },
    showAlert(id) {
      this.$swal({
        title: "Biztos törölni szeretnéd?",
        text: "",
        icon: "error",
        showCancelButton: true,
        confirmButtonText: "Törlés",
        cancelButtonText: "Mégsem"
      }).then(result => {
        if (result.value) {
          this.$swal("Sikeresen törölve", "", "success");
          axios
            .post(
              process.env.VUE_APP_GET_WALLET_API,
              JSON.stringify({ delete: id })
            )
            .then(response => (this.data.outlay = response.data))
            .catch(err => console.log(err));
        }
      });
    }
  }
};
</script>
